.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 5s linear;
  }
}
.Nav-Bar {
  background-color:black;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  width: 100% ;
}
.Master-List {
  display: flex;
  flex: 1;
  align-items: center;
  justify-content: center;
}
.App-header {

  background-color: #291641;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  justify-content:center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61e6c9;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
